@import url(https://fonts.googleapis.com/css2?family=Aboreto&family=Dancing+Script&family=Montserrat&family=Oswald&display=swap);
/*
  font-family: 'Aboreto', cursive;
  font-family: 'Dancing Script', cursive;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Oswald', sans-serif;
*/
.container-image{
  background-image: url(/static/media/pexels-henry-&-co-2341290.38012790.jpg);
}

.aboreto {
  font-family: 'Aboreto', cursive;
}
.dancing {
  font-family: 'Dancing Script', cursive;
}
.montserrat {
  font-family: 'Montserrat', sans-serif;
}
.oswald {
  font-family: 'Oswald', sans-serif;
}

.App {
  text-align: center;
  background-image: url(/static/media/pexels-henry-&-co-2341290.38012790.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: local; 
  min-width: 100vw;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  
}

.page-body {
  padding-bottom: 120px;
}

.proj-overlay {
  transition: .5s ease;
  opacity: 0;
  text-align: center;
}

.proj-image {
  transition: .5s ease;
  opacity: 1;
  text-align: center;
  height: 300px;
  object-fit: cover;
  /* max-width: 50%; */
}

.project:hover .proj-image {
  opacity: 0.3;
}

.project:hover .proj-overlay {
  opacity: 1;
}

.proj-overlay-text {
  background-color:dimgrey;
  border-radius: 25px;
}

.icon {
  width: 24px;
}
.icon:hover{
  cursor: pointer;
  opacity: 0.5;
}

.about-img {
  width: 256px;
}

.text-left {
  text-align: left;
  padding-left: 75px;
}

.nav-link:hover {
  border-bottom: 3;
  opacity: 0.5;
}

ul {
  list-style-type: none;
}

.dark-grey {
  color: darkslategray;
}

.a-over {
  text-decoration: none;
  color: darkslategray;
}

.link-custom:hover {
  cursor: pointer;
  opacity: 0.5;
  text-decoration: underline;
  font-size-adjust: 0.8;
}

.form-control-custom:focus, .form-control-custom:active, .form-control-custom {
  background: transparent;
  background-color: none;
  border: 1;
  border-radius: 25px;
  font-family: 'Montserrat', sans-serif;
}

.btn-custom {
  background: transparent;
  color: darkslategray;
  border-color: darkgray;
}
.btn-custom:hover {
  font-size-adjust: 0.8;
  color: darkgray;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}


